import { Suspense } from "react";
import { Link } from "react-router-dom";
import IndexNavbar from "../../components/Navbar/IndexNavbar";

const FallBack = () => {
  return (
    <div className="flex mx-auto  justify-center mt-36 w-2/3 bg-white py-6 ">
      <h4 className="text-black text-2xl font-bold">Loading ... .. .</h4>
    </div>
  );
};

interface Props {}

const LoginPage = (props: Props) => {
  const loginHandler = (e: any) => {
    e.preventDefault();
  };
  return (
    <Suspense fallback={<FallBack />}>
      <IndexNavbar />
      <div className="flex justify-center py-10 items-center  select-none">
        <div className="flex items-center  justify-center w-11/12 mx-5 lg:w-2/6 mb-10">
          <div className="flex flex-col mt-10 md:mt-12 lg:mt-24 bg-white shadow-sm px-10 py-8  rounded-md ">
            <h3 className="mb-2 text-md font-semibold sm:text-center sm:text-2xl">
              ðŸ‘‹ Welcome !
            </h3>
            <span className="mb-4  text-center text-sm text-gray-800 ">
              Log securely into your account
            </span>
            <form onSubmit={(e) => loginHandler(e)}>
              <div className="mb-1 sm:mb-2">
                <label
                  htmlFor="firstName"
                  className="inline-block mb-1 font-normal text-sm"
                >
                  Email or phone number
                </label>
                <input
                  placeholder="Email or phone number"
                  required
                  type="text"
                  className="flex-grow w-full h-10 text-sm px-4 mb-2 transition duration-200 bg-white border border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline"
                  id="firstName"
                  name="firstName"
                />
              </div>
              <div className="mb-1 sm:mb-2">
                <label
                  htmlFor="lastName"
                  className="inline-block mb-1 font-normal text-sm"
                >
                  Password
                </label>
                <input
                  placeholder="Enter password"
                  required
                  type="password"
                  className="flex-grow w-full h-10 text-sm text-gray-600 px-4 mb-2 transition duration-200 bg-white border  border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline hover:border-2  hover:border-orange-500 focus:ring-2  focus:ring-orange-500 "
                  id="lastName"
                  name="lastName"
                />
              </div>
              <div className="mt-4 mb-4 md:mb-4 flex justify-center">
                <button
                  type="submit"
                  className="inline-flex items-center justify-center w-full h-9  px-2 font-normal tracking-wide text-white transition duration-200 rounded shadow-md bg-orange-600 hover:bg-white hover:text-orange-600 hover:border-orange-500 hover:border-2 focus:shadow-outline focus:outline-none focus:shadow-none"
                >
                  <svg
                    className="w-6 h-6 mr-0.5"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M11 16l-4-4m0 0l4-4m-4 4h14m-5 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h7a3 3 0 013 3v1"
                    />
                  </svg>
                  Log In
                </button>
              </div>
              <p className="text-xs text-gray-600 sm:text-sm py-4">
                Forgotten password ?
                <Link
                  to="/reset-password"
                  className="font-medium tracking-wide text-orange-500 space-x-0"
                >
                  <span> Reset </span>
                </Link>
              </p>
              <p className="text-xs text-gray-600 sm:text-sm ">
                Don't have account ?
                <Link
                  to="/register"
                  className=" font-medium tracking-wide text-orange-500 space-x-0"
                >
                  <span> Register</span>
                </Link>
              </p>
            </form>
          </div>
        </div>
      </div>
    </Suspense>
  );
};

export default LoginPage;
